import React, { useContext, useEffect, useState } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom'

import axios from '../utils/axios'
import { Store } from '../utils/Store'
import s from './styles/Auth.module.scss'

const Auth = () => {
  const {pathname} = useLocation()
  const navigate = useNavigate()

  const { state, dispatch: ctxDispatch } = useContext(Store);
  const { userInfo } = state;

  const [name, setName] = useState('')
  const [fullName, setFullName] = useState('')
  const [phone, setPhone] = useState('')
  const [gmail, setGmail] = useState('')
  const [password, setPassword] = useState('')

  const submitHandler = async (e) => {
    e.preventDefault();
    try {
      if(isLogin){
        const { data } = await axios.post('/api/user/login', {
          gmail,
          password,
        });
        ctxDispatch({ type: 'USER_SIGNIN', payload: data });
        localStorage.setItem('userInfo', JSON.stringify(data));
        navigate( '/');
      } else {
        const { data } = await axios.post('/api/user/registration', {
          name,
          fullName,
          phone,
          gmail,
          password,
        });
        ctxDispatch({ type: 'USER_SIGNIN', payload: data });
        localStorage.setItem('userInfo', JSON.stringify(data));
        navigate( '/');
      }
    } catch (err) {
      console.log('errrorr');
      console.log(err)
    }
  };

  useEffect(() => {
    if(userInfo){
      navigate('/')
    }
  }, [userInfo]);

  const isLogin = pathname === '/login';
  const isRegistration = pathname ==='/registration';

  const [privacy, setPrivacy] = useState(false)

  const clickPrivacy = b => b ? setPrivacy(false) : setPrivacy(true) 
  
  return (
    <div>
      <div className="container">
        <div className={s.authbox}>
            <h1><span onClick={()=> navigate('/login')} className={isRegistration ? s.span : ''}></span> <span onClick={()=> navigate('/registration')} className={isLogin ? s.span : ''}></span> </h1>
          <form onSubmit={submitHandler}>

            <input className={s.check} id='remember' type="checkbox" />
            <label htmlFor="remember"> </label>

          { isRegistration && <> 
              <input type="text" placeholder='' required value={name} onChange={e => setName(e.target.value)} />
              <input type="test" placeholder=''  value={fullName} onChange={e => setFullName(e.target.value)} />
              <input type="number" placeholder=' ' required value={phone} onChange={e => setPhone(e.target.value)} />
            </>}

            <input type="gmail" placeholder='EMAIL ADRESS' required value={gmail} onChange={e => setGmail(e.target.value)} />
            <input type="password" placeholder='PASSWORD' required value={password} onChange={e => setPassword(e.target.value)} />

            { isLogin && <Link className={s.link}> ?</Link>}

            <div className={s.privacybox}>
              <div>
                <input className={s.check} id='privacy' type="checkbox" />
                <label htmlFor="privacy"></label>
              </div>

              <p onClick={()=> clickPrivacy(privacy)}>{privacy ? '' : '  '}</p>
              
            </div>
            {privacy && <p>
              1.                 27.07.2006. 152-    ( -    )             ,     (  ). <br />
              1.1.                      ,         ,    . <br />
              1.2.         (  )    ,       -
            </p>}

            <button type='submit'></button>
          </form>
        </div>
      </div>
    </div>
  )
}

export default Auth